.card {
  border: 1px solid #f3f3f3;
  width: $card-width;
  border-radius: 5px;
  color: $text-base-color;

  display: flex;
  flex-direction: column;

  @include generalTransition(transform);

  &__logo {
    display: flex;
    height: 134px;
    width: 160px;

    margin: 32px auto 40px;
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;

    margin: 0 16px 4px;

    @include generalTransition(color);
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    color: $text-secondary-color;
    margin: 0 16px;
  }

  &__review {
    display: flex;
    margin: 16px 16px 24px;
    justify-content: space-between;
    align-items: flex-end;

    &_number-of-reviews {
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 16px;

    &-text {
      font-size: 12px;
      line-height: 18px;
      color: $text-secondary-color;
    }

    &-amount {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__buy {
    height: 40px;
    background-color: $button-color;
    border: 1px solid $button-color;
    border-radius: 5px;
    color: #fff;
    margin: 16px;

    display: flex;
    justify-content: center;
    align-items: center;

    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;

    @include generalTransition(all);

    &:hover {
      background-color: #fff;
      color: $button-color;
    }
  }

  &:hover {
    transform: scale(120%);
  }

  &:hover &__name {
    color: $text-hover-color;
  }
}
